.App{
display:flex;
text-align: center;
background-color:rgb(36, 35, 35);
color:white;
position: absolute;
top:0px;left:0px;right:0px;bottom:0px;
}
.sidemenu{
  width:200px;
  padding:10px;
  background-color:#202123;
}
.side-menu-botton{
  padding:15px;
  border:1px solid white;
  border-radius: 5px;
  text-align: left;
  transition:.69 all;
}
.side-menu-botton:hover{
  background-color:rgb(255,255,255,0.1);
}
.side-menu-botton span{
  padding-left: 6px;
  padding-right: 10px;
}
.chatbox{
  flex:1;
  background-color:#343541;
  position:relative;
}
.chat-input-holder{
  padding:24px;
  position:absolute;
  bottom: 0px;left:0;right:0;
}
.chat-input-textarea{
  background-color:#40414f;
  width:95%;
  border-radius:.5px;
  border:none;
  outline:none;
  margin:4px;
  box-shadow:0 0px 8px 0px rgb(0,0,0,.25);
  padding:12px;
  font-size:1em;
  color:white;
}
.chat-log{
padding:24px;
text-align: left;
}
.chat-message.chatgpt{
background-color:#444654;
}
.chat-message-center{
max-width: 480px;
margin-left: auto;
margin-right: auto;
display: flex;
padding:12px;
padding-left: 24px;
padding-right: 24px;
}
.avtar{
background: white;
border-radius: 50%;
width: 40px;
height: 40px;
}
.avtar.chatgpt{
  background: #0da37f;
}
.message{
padding-left: 40px;
padding-right: 40px;
}
@media only screen and (max-width: 600px) {
  .sidemenu{
    width:100px;
  }
  .side-menu-botton{
    padding:10px;
    font-size: .6em;
  }
  .side-menu-botton span{
    padding-left: 1px;
    padding-right: 10px;
  }
  .chat-message-center{
    max-width: 480px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    padding:5px;
    padding-left: 1px;
    padding-right:1px;
    }
    .message{
      padding-left: 20px;
      padding-right: 10px;
      }
      .chat-input-textarea{
        width:80%;
      }
}